.dropdown {
  width: 176px;
  position: relative;

  &__label {
    font-family: var(--mont-semibold);
    font-size: 12px;
    line-height: 15px;
    display: block;
    margin-bottom: 4px;
    color: var(--c-secondary);
  }

  &__button {
    padding: 10px 12px;
    width: 100%;
  }

  &__options {
    margin-top: 4px;
    border: 1px solid var(--c-elements);
    background-color: var(--c-white);
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.05);
    position: absolute;
    width: 100%;
    z-index: 1;
  }

  &__link {
    display: flex;
    font-family: var(--c-medium);
    cursor: pointer;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 20px;
    color: var(--c-secondary);
    transition: background-color 0.3s ease,
      color 0.3s ease;

    &:hover,
    &.is-active {
      background-color: var(--c-hover_BG);
      color: var(--c-primary);
      font-weight: 500;
    }
  }
}
